<template>
    <div id="header">
        <h1 class="logo"><img src="../../assets/img/logo.png" alt=""></h1>
        <div class="header-right">
            <el-dropdown>
                <span class="el-dropdown-link">{{ username }}</span><i class="el-icon-arrow-down el-icon--right"></i>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item><span @click="loginOut">退出</span></el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                username: '',
                dialogVisible: false
            }
        },
        created:function () {
            this.username = localStorage.getItem('username')
        },
        methods: {
            loginOut:function () {
                this.$confirm('此操作将退出当前账户, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    localStorage.removeItem("username");
                    this.$router.push({path: '/'});
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消操作'
                    });
                });
            }
        }
    }
</script>

