import Vue from 'vue'
import Router from 'vue-router'
import login from '../components/login/login'
import home from '../components/home/home'
import deviceQuery from '../components/asset/deviceQuery'
import deviceRegister from '../components/asset/deviceRegister'


//构建组件
Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/home',
            name: 'home',
            component: home,
            children: [
                {
                    path: '/home/',
                    name: '/home/',
                    redirect: '/home/deviceQuery',
                    component: deviceQuery,
                    children: [
                        {
                            path: 'deviceQuery',
                            component: deviceQuery
                        }
                    ]
                },
                {
                    path: 'deviceRegister',
                    name: 'deviceRegister',
                    component: deviceRegister
                },
            ]
        },
        {
            path: '/',
            component: login
        }
    ]
})



