<template>
    <div id="login">
        <div class="loginBox">
            <h3>登陆</h3>
            <el-input
                    placeholder="用户名"
                    v-model="userNmae"
                    clearable>
            </el-input>

            <el-input
                    placeholder="密码"
                    v-model="passWord"
                    clearable
                    type="password">
            </el-input>

            <el-button @click="login" type="primary">登陆</el-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "login",
        data() {
            return {
                userNmae:'',
                passWord:''
            }
        },
        created:function () {

        },
        methods: {
            login:function () {
                if(!this.userNmae || !this.passWord){
                    this.$message({
                        message: '用户名或密码不能为空！',
                        type: 'warning'
                    });
                }else{
                    this.$router.push({path: '/home'});
                    localStorage.setItem('username', this.userNmae);
                }
            }
        }
    }

    // 存：
